export default {
	data() {
		return {
			accountList: [],
			goodsPrice: 0.00, //商品金额
			sumPrice: 0.00, //支付金额
			freight: 12.00 //运费
		}
	},
	onShow() {
		// 获取本地数据
		uni.getStorage({
			key: "accountList",
			success: (res) => {
				this.accountList = res.data
				// console.log("accountList", this.accountList)
				// 商品金额
				for (let i = 0; i < this.accountList.length; i++) {
					this.goodsPrice = this.goodsPrice + (this.accountList[i].number * this.accountList[i].price)
				}
				// 支付金额
				this.sumPrice = this.goodsPrice + this.freight;



			}
		})
	},
	filters: {
		toFixed(x) {
			return parseFloat(x).toFixed(2)
		}
	},
	onBackPress() {
		this.clearAccount
	},
	methods: {
		clearAccount() {
			uni.removeStorage({
				key: "accountList",
				success: (res) => {
					this.accountList = [];
				}
			})
		}
	}
}
